<template>
  <div class="container-returnDish">
    <headerUsually btnShow="true" titleText="单菜操作" url="/mobileOrdering/SDishOperation"></headerUsually>
    <div class="container-main">
      <div class="content-nav" v-if="type=='returnDish'">
        <span>数量</span>
        <input class="quantity" type="number" @change="change()" v-model="quantity" />
      </div>
      <div class="select-reason">
        <div
          class="item"
          v-for="(item,index) in listArr"
          :key="index"
          @click="reasonFun(item)"
        >{{item.descript}}</div>
      </div>
    </div>
  </div>
</template>
<script>
import ajaxGet from "@/conf/ajaxGet";
import { Toast } from "mint-ui";
import headerUsually from "@/components/common/mobileOrdering/headerUsually";
import { mapState, mapGetters } from "vuex";
export default {
  components: {
    headerUsually
  },
  data() {
    return {
      quantity: -1,
      listArr: [],
      type: ""
    };
  },
  computed: {
    ...mapState({
      modifySDish: state => state.mobileOrdering.modifySDish
    })
  },
  async created() {
    this.type = this.$route.params.type;

    this.quantity = this.modifySDish.number;
    let data = {
      params: {
        type: "pos_return_reason"
      }
    };
    let res = await ajaxGet("/pos/router?method=getCodeBase", data);
    if (res && res.resultCode == 0) {
      this.listArr = res.data;
    }
  },
  methods: {
    change() {
      if (this.quantity > this.modifySDish.number || this.quantity <= 0) {
        this.quantity = this.modifySDish.number;
      }
    },
    async reasonFun(item) {
      if (this.type == "returnDish") {
        if (this.quantity > this.modifySDish.number || this.quantity <= 0) {
        } else {
          let info = `[${item.code}]${item.descript}`;
          let data = {
            params: {
              proType: 4,
              pluState: this.modifySDish.pluState,
              accnt: this.modifySDish.accnt,
              orderId: this.modifySDish.id,
              num: this.quantity,
              info: info
            }
          };
          let res = await ajaxGet("/pos/router?method=updateCheckDetail", data);
          if (res && res.resultCode == 0) {
            this.msg("退菜成功！");
            this.$router.push("/mobileOrdering/myOrder");
          }
        }
      } else {
        let info = `[${item.code}]${item.descript}`;
        let data = {
          params: {
            accnt: this.modifySDish.accnt,
            detailId: this.modifySDish.id,
            info: info
          }
        };
        let res = await ajaxGet("/pos/router?method=updateRevokePlu", data);
        if (res && res.resultCode == 0) {
          this.msg("冲销菜！");
          this.$router.push("/mobileOrdering/myOrder");
        }
      }
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "middle",
        duration: 3000
      });
    }
  }
};
</script>
<style lang="less" scoped>
.container-returnDish {
  padding-top: 46px;
  .container-main {
    .content-nav {
      display: flex;
      align-content: center;
      justify-content: space-between;
      font-size: 14px;
      color: #000000;
      padding: 17px 15px;
      background-color: #fff;
      margin-bottom: 10px;
      .quantity {
        flex: 1;
        text-align: center;
        outline: none;
        border: none;
      }
    }
    .select-reason {
      background-color: #fff;
      padding-left: 15px;
      .item {
        text-align: left;
        padding: 17px 0;
        border-bottom: 1px solid #ddd;
      }
    }
  }
}
</style>




